.container {
  display: flex;
  width: 100%;
  height: 100%;
  background-color: #e0e0e0;
}

.navbar {
  background-color: #000;
  margin: 0;
  padding: 0;
}

.navbar a {
  display: inline-block;
  padding: 10px 20px;
  color: #fff;
  text-decoration: none;
  width: 100%;
}

.navbar a.active {
  background-color: blue;
}

.content {
  flex: 1 1 0;
  padding: 30px;
}

.demo canvas {
  background-color: #fff;
}

@media (max-width: 576px) {
  .container {
    flex-direction: column;
  }

  .navbar li {
    display: inline-block;
  }
}
